export default {
    name: "activeTime",
  data(){
      return{
        radio:'1',
        api: this.GLOBAL.baseURL,
        banIndex:'',
        active_id:'',
        complete:''
      }
  },
  watch:{
      radio:function(){
        if(this.radio == '1'){
          this.$router.push({
              name: 'Ltime',
              query:{
              information: this.active_id
            }
          })
          localStorage.setItem('Time','Ltime')
        }else if(this.radio == '2'){
          this.$router.push({
            name: 'moreTime',
            query:{
              information: this.active_id
            }
          })
          localStorage.setItem('Time','moreTime')
        }
      }
  },
  methods:{
    notChange(val){
      this.banIndex = val
    },
    saveId(res){
      this.$emit('saveId',res)
    },
    changeRouter(res){
      this.$emit('changeRouter',{id:res.id,router:res.router,information:res.information,complete: this.complete})
    },
    getActives(){
      this.$http.post(this.api + '/ActivityE',{
        token: localStorage.getItem('token'),
        activity_id: this.active_id,
        visit: 0
      })
        .then(res=>{
          if(res.data.code == 1){
            let data = res.data.data
            if(data.long_day == 1 ){
              this.radio = '2'
            }else{
              this.radio = '1'
            }
          }else if(res.data.code == 3){
            this.getActives()
          }else if(res.data.code == 0){
            alert(res.data.msg)
          }
        })
    }
  },
  mounted() {
    window.scroll(0,0)
      let _this = this
    if(this.active_id){
      _this.getActives()
    }
  },
  created(){
      this.active_id = this.$route.query.information
    this.complete = this.$route.query.complete
  },
  destroyed(){
      localStorage.removeItem('Time')
  }
}



